body {
  font-family: "Raleway", Verdana, sans-serif;
}

.button, h3 {
  font-family: "Open Sans", Verdana, sans-serif;
}

html {
  font-size: 10px;
}

body {
  font-style: normal;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.1rem;
}

h1 {
  font-style: normal;
  font-weight: 500;
  font-size: 4.8rem;
  line-height: 5.6rem;
}

h2 {
  font-style: normal;
  font-weight: 400;
  font-size: 3.6rem;
  line-height: 4.3rem;
}

h3 {
  font-style: normal;
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 3.3rem;
}

.button {
  padding: 26px 91px;
  font-style: normal;
  font-weight: 600;
  font-size: 1.8rem;
  line-height: 2.4rem;
  cursor: pointer;
  transition: all 0.3s;
}
.button_bordered {
  background: none;
  border: 2px solid #FFFFFF;
  color: #FFFFFF;
}
.button_bordered:hover {
  color: #EC7979;
  border-color: #EC7979;
}
.button_bordered:active {
  background: #AB4949;
  border-color: #AB4949;
}
.button_colored {
  background: #EC7979;
  color: #FFFFFF;
  border: 1px solid #EC7979;
}
.button_colored:hover {
  background: #AB4949;
  border-color: #AB4949;
}
.button_colored:active {
  border-color: #AB4949;
}

.wrapper {
  max-width: 1170px;
  margin: auto;
}

.header {
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.0509803922);
  background: #FFFFFF;
  padding: 15px 0 17px 0;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__buttons > .button:first-child {
  margin-left: 14px;
  margin-left: 0;
}

.layout-3-column {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.layout-3-column > * {
  flex-basis: 30%;
}

.layout-2-column {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.layout-2-column > * {
  flex-basis: 48%;
}

